<template>
  <div id="app">
    <audio id="aa" preload="auto" src="hmm.mp3" type="audio/mp3"></audio>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide">
          <div class="start-bg"></div>
          <div class="start-title" v-html="startTitle"></div>
          <div class="start-btn" v-show="showStar"><span class="emoji emoji2764" title="heavy black heart"></span>️</div>
          <canvas id="star"></canvas>
        </div>
        <div class="swiper-slide">
          <div class="c1 content"></div>
          <div class="title" v-html="title1"></div>
          <img src="./assets/IMG_1239.gif">
        </div>
        <div class="swiper-slide">
          <div class="content c2"></div>
          <div class="title" v-html="title2"></div>
          <img src="./assets/IMG_1240.gif">
        </div>
        <div class="swiper-slide">
          <div class="content c3"></div>
          <div class="title" v-html="title3"></div>
          <img src="./assets/IMG_1241.gif">
        </div>
        <div class="swiper-slide">
          <div class="content c4"></div>
          <div class="title" v-html="title4"></div>
          <img src="./assets/IMG_1242.gif">
        </div>
        <div class="swiper-slide">
          <div class="content c2"></div>
          <div class="title" v-html="title5"></div>
          <img src="./assets/IMG_1243.gif">
        </div>
        <div class="swiper-slide">
          <div class="content c3"></div>
          <div class="title" v-html="title6"></div>
          <img src="./assets/IMG_1244.gif">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      playing: false,
      showStar: false,
      startTitle: '',
      title1: "<div style = 'padding-right: 5vw'>愿你一蹦就能见到想见的人 <span class='emoji emoji1f496' title='sparkling heart'></span></div>",
      title2: "<div style = 'padding-right: 5vw'>愿你头一歪就是想依靠的人 <span class='emoji emoji1f496' title='sparkling heart'></span></div>",
      title3: "<div style = 'padding-right: 5vw'>愿你的小脾气有人会懂 <span class='emoji emoji1f496' title='sparkling heart'></span></div>",
      title4: "<div style = 'padding-right: 5vw'>愿你难过的时候会有人哄你 <span class='emoji emoji1f496' title='sparkling heart'></span></div>",
      title5: "<div style = 'padding-right: 5vw'>愿你想玩的时候有人陪你闹 <span class='emoji emoji1f496' title='sparkling heart'></span></div>",
      title6: "<div style = 'padding-right: 5vw'>愿你一难过就有逗你开心的人 <span class='emoji emoji1f496' title='sparkling heart'></span></div>"
    }
  },
  methods: {
    play () {
      if (this.playing) {
        return
      }
      let audio = document.getElementById('aa')
      audio.loop = true
      audio.currentTime = 11
      if('fastSeek' in audio){
        audio.fastSeek(11);//改变audio.currentTime的值
      }
      audio.play();
      this.playing = true
    },
    draw(){
      let self = this
      var drawing = document.getElementById("star"); //获取canvas元素
      drawing.width = '200'; //设置画布大小
      drawing.height = '200';
      if (drawing.getContext){  //获取绘图上下文
          var content = drawing.getContext("2d"),
              radian = 0,   //设置初始弧度
              radian_add = Math.PI/40;  //设置弧度增量
          content.beginPath();  //开始绘图
          content.translate(50,50);  //设置绘图原点
          content.moveTo(this.getX1(radian),this.getY1(radian)); //移动绘图游标至原点

          let aa = setInterval(() => {
              radian += radian_add;
              let X = this.getX1(radian);
              let Y = this.getY1(radian);
              content.lineTo(X,Y);
              content.fillStyle = 'red'
              content.strokeStyle = "red";  //设置描边样式
              content.stroke();  //对路径描边
              if (radian > Math.PI * 2) {
                  clearInterval(aa)
                  self.$el.querySelector('#star').addEventListener('click', ()=> {
                    self.play();
                    self.swiper.slideNext(600)
                    self.slide()
                  }, false)
              }
          }, 50)
          
      }
    },
    slide () {
      setTimeout(() => {
        this.swiper.slideNext(600)
        this.slide();
      }, 4000)
    },
    getX1(t){  //获取心型线的X坐标
      return 3 * (16*Math.pow(Math.sin(t),3))
    },
    getY1(t){  //获取心型线的Y坐标
      return -3 * (13*Math.cos(t)-5*Math.cos(2*t)-2*Math.cos(3*t)-Math.cos(4*t))
    }
  },
  mounted () {
    let mySwiper = new window.Swiper('.swiper-container', {
      loop: false,
      direction: 'vertical'
    })
    this.swiper = mySwiper
    // eslint-disable-next-line no-console
    console.log(window.jEmoji.unifiedToHTML("❤️"))
    let title = '小傻瓜，准备好了么'
    let index = 0
    let self = this
    document.addEventListener("DOMContentLoaded", function(){
      let interval = setInterval(() => {
        self.startTitle += title[index]
        index ++
        if (index === title.length) {
          clearInterval(interval)
          self.showStar = true
          self.draw()
        }
      }, 300)
    }); 
   
  }
}
</script>

